<template>
  <el-card class="box-card">
    <el-button type="primary" @click="$router.push('/admin/main/employee/add-hire')">Hire Employee</el-button>
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="username">
        <el-input placeholder="username" v-model="param.username"></el-input>
      </el-form-item>
      <el-form-item label="id">
        <el-input placeholder="id" v-model="param.id"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchForm">Query</el-button>
      </el-form-item>
    </el-form>
    <el-table
        border
        ref="multipleTable"
        :data="tableList"
        tooltip-effect="dark"
        style="width: 100%"
    >
      <el-table-column
          align="center"
          label="username"
          prop="username"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          prop="id"
          label="id"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="operation"
      >
        <template v-slot="scope">
          <el-button type="danger" size="small" @click="handleDelete(scope.row.username, scope.row.id)">Delete</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="changePage"
    >
    </el-pagination>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        username: '',
        id: '',
      },
      tableList: [],
      pageInfo: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0
    }
  },
  mounted() {
    this.getTableList(this.pageInfo)
  },
  methods: {
    getTableList(){
      this.$axios.post('/workFor/findAll', Object.assign(this.param, this.pageInfo)).then(res => {
        this.tableList = res.data.rows;
        this.total = res.data.count;
      })
    },
    searchForm(){
      this.pageInfo.pageNum = 1;
      this.getTableList();
    },
    changePage(e){
      this.pageInfo.pageNum = e
      this.getTableList()
    },
    handleDelete(username, id){
      this.$confirm('Are you sure to delete this ?', 'Warning', {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel',
        type: 'warning'
      }).then(() => {
        this.$axios.get('/workFor/remove', {
          params: {username, id}
        }).then(() => {
          this.$message({
            type: 'success',
            message: 'operate successfully!'
          });
          this.getTableList();
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: 'operation cancel'
        });
      });
    }
  },

}
</script>

<style scoped lang="scss">
.demo-form-inline, .el-pagination {
  margin-top: 10px;
}
</style>
